import React from 'react';
import { useAppContext } from '../../../context/AppContext';
import './IntroductionSection.css';
import shopImage from '../../../imgs/shop.jpg';

const IntroductionSection = () => {
    const { darkMode } = useAppContext();
    return (
        <section className={`introduction-section ${darkMode ? 'dark' : 'light'}`}>
            <div className="text-content">
                <h2>Relojera Bentez - Tradicin en Encarnacin</h2>
                <p>
                    Desde sus inicios en la zona baja de Encarnacin, Raimundo Bentez ha construido su negocio con dedicacin y amor.
                    Tras su rehubicacin, nuestra relojera se estableci en el circuito comercial, donde hoy seguimos ofreciendo
                    calidad y elegancia en cada pieza.
                </p>
            </div>
            <div className="image-container">
                <img src={shopImage} alt="Relojera Bentez" className="shop-image" />
            </div>
        </section>
    );
};

export default IntroductionSection;
